import {
    Chart,
    ChartConfiguration,
    LineController,
    LineElement,
    PointElement,
    LinearScale,
    TimeScale,
    Tooltip,
    Legend,
    Title,
    Filler,
    ChartData,
    ChartOptions,
    Point,
    TimeScaleOptions
} from 'chart.js';
import 'chartjs-adapter-date-fns';
import { loadAllCoinsData, displayLatestUpdate } from './modules/data.loader';
import { AvailableCoinsComponent } from './modules/available-coins.component';
import { router } from './modules/router';
import { initShareButton } from './modules/share-component';

// Register Chart.js components
Chart.register(
    LineController,
    LineElement,
    PointElement,
    LinearScale,
    TimeScale,
    Tooltip,
    Legend,
    Title,
    Filler
);

interface DcaSettings {
    purchaseAmount: number;
    repeatPurchase: 'weekly' | 'monthly' | 'quarterly';
    accumulateFor: number;
    startingDate: Date;
}

interface DcaResult {
    totalInvested: number;
    totalValue: number;
    percentChange: number;
    coinAmount: number;
    startDate: Date;
    endDate: Date;
    purchaseHistory: Array<{
        date: Date;
        price: number;
        investment: number;
        coinAmount: number;
    }>;
    postAccumulationHistory: Array<{
        date: Date;
        price: number;
    }>;
}

type TimeDataPoint = {
    x: Date;
    y: number;
};

const coinId_default = 'bitcoin';
const purchaseFrequency_default = 'monthly';
const purchaseAmount_default = 100;
const accumulateFor_default = 24;
const startingFrom_default = 24;

let coinId = coinId_default;
let purchaseFrequency = purchaseFrequency_default;
let purchaseAmount = purchaseAmount_default;
let accumulateFor = accumulateFor_default;
let startingFrom = startingFrom_default;

class DcaCalculator {
    private chart: Chart<'line', TimeDataPoint[]> | null = null;
    private selectedCoinId: string = coinId_default;
    private coin_markets: any[] = [];
    private updateUrlCallback: () => void;

    constructor(updateUrlCallback: () => void) {
        this.updateUrlCallback = updateUrlCallback;
        this.initializeChart();
        this.attachEventListeners();
    }

    private initializeChart() {
        const canvas = document.getElementById('dcaChart') as HTMLCanvasElement;
        if (!canvas) return;

        const now = new Date();
        const oneYearAgo = new Date(now);
        oneYearAgo.setFullYear(now.getFullYear() - 1);
        const twoYearsAgo = new Date(now);
        twoYearsAgo.setFullYear(now.getFullYear() - 2);

        const initialData: TimeDataPoint[] = [
            { x: twoYearsAgo, y: 0 },
            { x: oneYearAgo, y: 0 },
            { x: now, y: 0 }
        ];

        const config: ChartConfiguration<'line', TimeDataPoint[]> = {
            type: 'line',
            data: {
                datasets: [{
                    label: 'Portfolio Value',
                    data: initialData,
                    borderColor: '#28a745',
                    backgroundColor: 'rgba(40, 167, 69, 0.1)',
                    fill: true,
                    tension: 0,
                    stepped: true
                }, {
                    label: 'Total Invested',
                    data: initialData,
                    borderColor: '#007bff',
                    backgroundColor: 'rgba(0, 123, 255, 0.1)',
                    fill: true,
                    tension: 0,
                    stepped: true
                }]
            },
            options: {
                responsive: true,
                maintainAspectRatio: false,
                plugins: {
                    title: {
                        display: true, text: 'Portfolio Value vs Total Invested'
                    },
                    tooltip: {
                        mode: 'index',
                        intersect: false,
                        callbacks: {
                            label: function(context) {
                                let label = context.dataset.label || '';
                                if (label) {
                                    label += ': ';
                                }
                                if (context.parsed.y !== null) {
                                    label += new Intl.NumberFormat('en-US', {
                                        style: 'currency',
                                        currency: 'USD'
                                    }).format(context.parsed.y);
                                }
                                return label;
                            }
                        }
                    }
                },
                scales: {
                    x: {
                        type: 'time',
                        time: {
                            unit: 'month',
                            displayFormats: {
                                month: 'MMM yyyy'
                            }
                        },
                        title: { display: true, text: 'Date' },
                        ticks: {
                            source: 'auto',
                            maxRotation: 45,
                            minRotation: 45
                        }
                    },
                    y: {
                        beginAtZero: true,
                        title: { display: true, text: 'Amount ($)' },
                        ticks: {
                            callback: function(value) {
                                if (typeof value === 'number') {
                                    return new Intl.NumberFormat('en-US', {
                                        style: 'currency', currency: 'USD',
                                        minimumFractionDigits: 0, maximumFractionDigits: 0
                                    }).format(value);
                                }
                                return '';
                            }
                        }
                    }
                }
            }
        };
        this.chart = new Chart(canvas, config);
    }

    private attachEventListeners() {
        const accumulateForSelect = document.getElementById('accumulateFor') as HTMLSelectElement;
        const startingDateSelect = document.getElementById('startingDate') as HTMLSelectElement;

        // Handle accumulateFor changes
        accumulateForSelect.addEventListener('change', (e) => {
            e.stopPropagation();
            const accumulateValue = Number(accumulateForSelect.value);
            const startingValue = Number(startingDateSelect.value);
            
            // If accumulate value is greater than starting value, revert accumulate value to starting value
            if (accumulateValue > startingValue) {
                accumulateForSelect.value = startingValue.toString();
            }
            
            // Let the URL change trigger the update
            this.updateUrlCallback(); 
        });

        // Handle startingDate changes
        startingDateSelect.addEventListener('change', (e) => {
            e.stopPropagation();
            const accumulateValue = Number(accumulateForSelect.value);
            const startingValue = Number(startingDateSelect.value);
            
            // If starting value is less than accumulate value, revert starting value to previous value
            if (startingValue < accumulateValue) {
                startingDateSelect.value = accumulateValue.toString();
            }
            
            // Let the URL change trigger the update
            this.updateUrlCallback();
        });

        // Handle other inputs
        const otherInputs = ['purchaseAmount', 'repeatPurchase'];
        otherInputs.forEach(id => {
            const element = document.getElementById(id);
            if (element) {
                element.addEventListener('change', () => {
                    // Let the URL change trigger the update
                    this.updateUrlCallback();
                });
            }
        });
    }

    public getSettings(): DcaSettings {
        const purchaseAmount = document.getElementById('purchaseAmount') as HTMLInputElement;
        const repeatPurchase = document.getElementById('repeatPurchase') as HTMLSelectElement;
        const accumulateFor = document.getElementById('accumulateFor') as HTMLSelectElement;
        const startingDate = document.getElementById('startingDate') as HTMLSelectElement;

        return {
            purchaseAmount: Number(purchaseAmount.value),
            repeatPurchase: repeatPurchase.value as 'weekly' | 'monthly' | 'quarterly',
            accumulateFor: Number(accumulateFor.value),
            startingDate: new Date(Date.now() - Number(startingDate.value) * 30 * 24 * 60 * 60 * 1000)
        };
    }

    public updateSettings(settings: {
        coinId?: string;
        purchaseAmount?: number;
        repeatPurchase?: 'weekly' | 'monthly' | 'quarterly';
        accumulateFor?: number;
        startingFrom?: number;
    }) {
        if (settings.coinId) {
            this.selectedCoinId = settings.coinId;
        }
        if (settings.purchaseAmount) {
            const input = document.getElementById('purchaseAmount') as HTMLInputElement;
            if (input) input.value = settings.purchaseAmount.toString();
        }
        if (settings.repeatPurchase) {
            const select = document.getElementById('repeatPurchase') as HTMLSelectElement;
            if (select) select.value = settings.repeatPurchase;
        }
        if (settings.startingFrom) {
            const startingSelect = document.getElementById('startingDate') as HTMLSelectElement;
            const accumulateSelect = document.getElementById('accumulateFor') as HTMLSelectElement;
            if (startingSelect && accumulateSelect) {
                const accumulateValue = Number(accumulateSelect.value);
                // Only update starting value if it's not less than accumulate value
                if (settings.startingFrom >= accumulateValue) {
                    startingSelect.value = settings.startingFrom.toString();
                }
            }
        }
        if (settings.accumulateFor) {
            const accumulateSelect = document.getElementById('accumulateFor') as HTMLSelectElement;
            const startingSelect = document.getElementById('startingDate') as HTMLSelectElement;
            if (accumulateSelect && startingSelect) {
                const startingValue = Number(startingSelect.value);
                // Only set accumulate value if it's not greater than starting value
                if (settings.accumulateFor <= startingValue) {
                    accumulateSelect.value = settings.accumulateFor.toString();
                } else {
                    accumulateSelect.value = startingValue.toString();
                }
            }
        }
    }

    private async calculate(settings: DcaSettings): Promise<DcaResult> {
        const response = await fetch(`data/history/${this.selectedCoinId}.csv`);
        const csvText = await response.text();
        const history = csvText.split('\n')
            .slice(1) // Skip header row
            .filter(line => line.trim()) // Remove empty lines
            .map(line => {
                const [date, marketCap, open] = line.split(',');
                return {
                    date: new Date(date),
                    marketCap: Number(marketCap),
                    open: Number(open)
                };
            })
            .sort((a, b) => a.date.getTime() - b.date.getTime()); // Sort by date ascending

        const purchaseHistory: DcaResult['purchaseHistory'] = [];
        const postAccumulationHistory: DcaResult['postAccumulationHistory'] = []; // Initialize
        let totalInvested = 0;
        let totalCoinAmount = 0;

        // Determine the full period end date based on the startingDate setting (which represents months ago)
        // We need the original 'startingFrom' value for this. Let's get it from the DOM again for simplicity here.
        const startingDateSelect = document.getElementById('startingDate') as HTMLSelectElement;
        const startingFromMonths = Number(startingDateSelect.value);
        const fullPeriodStartDate = settings.startingDate; // This is already calculated correctly
        const fullPeriodEndDate = new Date(); // Today

        // Filter history for the entire period defined by 'startingDate' select
        const fullHistoryRange = history.filter(entry => entry.date >= fullPeriodStartDate);
        if (fullHistoryRange.length === 0) {
             // Handle case with no data in the selected range
             // console.warn("No historical data found for the selected period."); // Keep console clean
             return {
                 totalInvested: 0, totalValue: 0, percentChange: 0, coinAmount: 0,
                 startDate: fullPeriodStartDate, endDate: fullPeriodEndDate, purchaseHistory: [], postAccumulationHistory: []
             };
        }


        // Calculate the accumulation end date based on accumulateFor
        const accumulationEndDate = new Date(settings.startingDate);
        accumulationEndDate.setMonth(accumulationEndDate.getMonth() + settings.accumulateFor);

        // Generate target investment dates based on frequency, but only up to accumulationEndDate
        const targetInvestmentDates: Date[] = [];
        let currentInvestmentDate = new Date(settings.startingDate);
        
        // Loop while the potential investment date is strictly within the accumulation period
        while (currentInvestmentDate < accumulationEndDate) { // Use '<' to exclude the end date boundary
            targetInvestmentDates.push(new Date(currentInvestmentDate));
            
            // Increment based on frequency
            switch (settings.repeatPurchase.toLowerCase()) {
                case 'weekly':
                    currentInvestmentDate.setDate(currentInvestmentDate.getDate() + 7);
                    break;
                case 'monthly':
                    currentInvestmentDate.setMonth(currentInvestmentDate.getMonth() + 1);
                    break;
                case 'quarterly':
                    currentInvestmentDate.setMonth(currentInvestmentDate.getMonth() + 3);
                    break;
            }
        }


        // --- Investment Calculation Phase (within accumulation period) ---
        
        // For each target investment date, find the closest available price within the full history range
        for (const targetDate of targetInvestmentDates) {
            // Find the closest available date for price in the full history
            let closestEntry = fullHistoryRange[0];
            let minTimeDiff = Infinity;

            for (const entry of fullHistoryRange) {
                // Only consider history points up to or slightly past the target date for investment price
                if (entry.date >= targetDate) { 
                    const timeDiff = Math.abs(entry.date.getTime() - targetDate.getTime());
                    if (timeDiff < minTimeDiff) {
                        minTimeDiff = timeDiff;
                        closestEntry = entry;
                    }
                    // Optimization: Since history is sorted, once we pass the target date significantly, we can stop searching closer
                    if (entry.date > targetDate && timeDiff > minTimeDiff) break; 
                } else if (entry === fullHistoryRange[fullHistoryRange.length - 1]) {
                     // If we reach the end and haven't found a date >= target, use the last one
                     closestEntry = entry;
                }
            }
            
            // Ensure we found a valid entry
            if (!closestEntry) continue; 

            const price = closestEntry.open;
            const investment = settings.purchaseAmount;
            const coinAmount = investment / price;

            totalInvested += investment;
            totalCoinAmount += coinAmount;

            purchaseHistory.push({
                date: targetDate, // Use the intended investment date
                price,
                investment,
                coinAmount
            });
        }

        // --- Post-Accumulation Phase ---

        // Find the index of the first history entry after the accumulation period ended
        const firstPostAccumulationIndex = fullHistoryRange.findIndex(entry => entry.date > accumulationEndDate);

        if (firstPostAccumulationIndex !== -1) {
            // Add remaining history points after accumulation ends
            for (let i = firstPostAccumulationIndex; i < fullHistoryRange.length; i++) {
                const entry = fullHistoryRange[i];
                postAccumulationHistory.push({
                    date: entry.date,
                    price: entry.open
                });
            }
        }


        // --- Final Calculation ---

        // Use the last available price from the *entire* filtered history range for final value calculation
        const finalPriceEntry = fullHistoryRange[fullHistoryRange.length - 1];
        const finalPrice = finalPriceEntry.open;
        const totalValue = totalCoinAmount * finalPrice; // Value based on final accumulated coins
        const percentChange = totalInvested > 0 ? ((totalValue - totalInvested) / totalInvested) * 100 : 0; // Avoid division by zero

        return {
            totalInvested,
            totalValue,
            percentChange,
            coinAmount: totalCoinAmount,
            startDate: fullPeriodStartDate, // Use the actual start date of the whole period
            endDate: finalPriceEntry.date, // Use the date of the last data point in the full range
            purchaseHistory,
            postAccumulationHistory // Include the post-accumulation data
        };
    } // Correctly close the calculate method

    private updateUI(result: DcaResult) { // Start the updateUI method definition
        const totalInvested = document.getElementById('totalInvested');
        const totalValue = document.getElementById('totalValue');
        const percentChange = document.getElementById('percentChange');

        if (totalInvested) totalInvested.textContent = `$${result.totalInvested.toLocaleString(undefined, { maximumFractionDigits: 2 })}`;
        if (totalValue) totalValue.textContent = `$${result.totalValue.toLocaleString(undefined, { maximumFractionDigits: 2 })}`;
        if (percentChange) {
            const sign = result.percentChange >= 0 ? '+' : '';
            percentChange.textContent = `${sign}${result.percentChange.toFixed(2)}%`;
            percentChange.className = `result-value ${result.percentChange >= 0 ? 'positive' : 'negative'}`;
        }

        this.updateChart(result);
    }

    private updateChart(result: DcaResult) {
        if (!this.chart) return;

        // Calculate cumulative invested amount and portfolio value over time
        let cumulativeInvested = 0;
        let cumulativeCoins = 0;
        const chartData: TimeDataPoint[] = result.purchaseHistory.map(entry => {
            cumulativeInvested += entry.investment;
            cumulativeCoins += entry.coinAmount;
            return {
                x: entry.date,
                y: cumulativeCoins * entry.price
            };
        });

        // Get the final state after accumulation
        const finalCumulativeCoins = result.coinAmount;

        // Process post-accumulation history
        const postAccumulationChartData: TimeDataPoint[] = result.postAccumulationHistory.map(entry => ({
            x: entry.date,
            y: finalCumulativeCoins * entry.price
        }));

        // Combine the data points and ensure we have at least 2 points for each dataset
        const fullChartData = [...chartData, ...postAccumulationChartData];
        
        // Ensure we have at least 2 points for each dataset
        if (fullChartData.length < 2) {
            // If we only have one point, add a dummy point to prevent curve interpolation issues
            const singlePoint = fullChartData[0];
            if (singlePoint) {
                const dummyDate = new Date(singlePoint.x);
                dummyDate.setDate(dummyDate.getDate() + 1);
                fullChartData.push({
                    x: dummyDate,
                    y: singlePoint.y
                });
            }
        }

        // Create invested data showing cumulative investment over time
        let currentInvested = 0;
        const investedData: TimeDataPoint[] = result.purchaseHistory.map(entry => {
            currentInvested += entry.investment;
            return {
                x: entry.date,
                y: currentInvested
            };
        });

        // For post-accumulation period, keep the final investment amount
        const finalInvestment = investedData.length > 0 ? investedData[investedData.length - 1].y : 0;
        const postAccumulationInvestedData: TimeDataPoint[] = result.postAccumulationHistory.map(entry => ({
            x: entry.date,
            y: finalInvestment
        }));

        // Combine investment data
        const fullInvestedData = [...investedData, ...postAccumulationInvestedData];

        // Set portfolio value color based on final percent change
        const isProfitable = result.percentChange > 0;
        this.chart.data.datasets[0].borderColor = isProfitable ? '#28a745' : '#dc3545';
        this.chart.data.datasets[0].backgroundColor = isProfitable ? 'rgba(40, 167, 69, 0.1)' : 'rgba(220, 53, 69, 0.1)';

        this.chart.data.datasets[0].data = fullChartData;
        this.chart.data.datasets[1].data = fullInvestedData;
        this.chart.update();
    }

    public async updateCalculation() {
        const settings = this.getSettings();
        const result = await this.calculate(settings);
        this.updateUI(result);
    }

    public async selectCoin(coinId: string) {
        this.selectedCoinId = coinId;
        const selectedCoinName = document.getElementById('selectedCoinName');
        if (selectedCoinName) {
            const coin = this.coin_markets.find(c => c.id === coinId);
            if (coin) {
                selectedCoinName.textContent = `$${coin.symbol.toUpperCase()}`;
            }
        }

        // Clear the search input and reset the filter
        const searchInput = document.querySelector('.coin-search') as HTMLInputElement;
        if (searchInput) {
            searchInput.value = '';
            // Trigger the input event to update the component
            searchInput.dispatchEvent(new Event('input'));
        }
    }

    public setCoinMarkets(markets: any[]) {
        this.coin_markets = markets;
    }

    public getSelectedCoinId(): string {
        return this.selectedCoinId;
    }
}


// Initialize the calculator when the DOM is loaded
document.addEventListener('DOMContentLoaded', async () => {
    // Update URL when settings change
    const updateUrlWithSettings = () => {
        const settings = calculator.getSettings();
        const query: any = {};
        
        if (settings.purchaseAmount !== purchaseAmount_default) {
            query.amount = settings.purchaseAmount;
        }
        if (settings.repeatPurchase !== purchaseFrequency_default) {
            query.frequency = settings.repeatPurchase;
        }
        if (settings.accumulateFor !== accumulateFor_default) {
            query.accumulate = settings.accumulateFor;
        }
        if (settings.startingDate) {
            const monthsAgo = Math.round((Date.now() - settings.startingDate.getTime()) / (30 * 24 * 60 * 60 * 1000));
            if (monthsAgo !== startingFrom_default) {
                query.starting = monthsAgo;
            }
        }
        if (calculator.getSelectedCoinId() !== coinId_default) {
            query.coin = calculator.getSelectedCoinId();
        }
        
        router.navigate('/', query);
    };

    const calculator = new DcaCalculator(updateUrlWithSettings);
    
    // Load initial data
    const { coin_markets, latest_update } = await loadAllCoinsData();
    displayLatestUpdate(latest_update);

    // Set coin markets in calculator
    calculator.setCoinMarkets(coin_markets);

    // Initialize share button
    initShareButton(document.querySelector('.share-button'));

    // Initialize available coins component
    const availableCoinsComponent = new AvailableCoinsComponent(
        document.querySelector('.available-coins-container')!,
        new Set(), // Empty set since we don't track selected coins in DCA
        coin_markets,
        (coinId) => {
            calculator.selectCoin(coinId);
            window.scrollTo({ top: 0, behavior: 'smooth' });
        }
    );

    // Handle route changes
    router.on('/', async (params, query) => {
        const settings: any = {};
        
        if (query.coin) {
            const coinId = Array.isArray(query.coin) ? query.coin[0] : query.coin;
            settings.coinId = coinId;
        }
        if (query.amount) {
            const amount = Number(Array.isArray(query.amount) ? query.amount[0] : query.amount);
            settings.purchaseAmount = amount;
        }
        if (query.frequency) {
            const freq = Array.isArray(query.frequency) ? query.frequency[0] : query.frequency;
            if (['weekly', 'monthly', 'quarterly'].includes(freq.toLowerCase())) {
                settings.repeatPurchase = freq.toLowerCase() as 'weekly' | 'monthly' | 'quarterly';
            }
        }
        if (query.accumulate) {
            const accumulate = Number(Array.isArray(query.accumulate) ? query.accumulate[0] : query.accumulate);
            settings.accumulateFor = accumulate;
        }
        if (query.starting) {
            const starting = Number(Array.isArray(query.starting) ? query.starting[0] : query.starting);
            settings.startingFrom = starting;
        }

        // Apply all settings at once
        calculator.updateSettings(settings);
        await calculator.updateCalculation();
    });

    // Trigger initial route handler to apply any query parameters from hash
    const hash = window.location.hash.slice(1); // Remove the # symbol
    const params = new URLSearchParams(hash);
    router.navigate('/', Object.fromEntries(params));

    // Update the selectCoin method to update URL
    const originalSelectCoin = calculator.selectCoin;
    calculator.selectCoin = async (coinId: string) => { // Make it async again to match original signature
        await originalSelectCoin.call(calculator, coinId); // Update internal state first (await original async method)
        updateUrlWithSettings(); // Then update the URL
    };

    // Initial calculation needs to happen after settings are applied by router
    // The router.navigate call below will trigger the initial calculation via the handler
});
